.ReactModalPortal > div {
	opacity: 0;
}
.ReactModalPortal .ReactModal__Overlay {
	display: flex;
	justify-content: center;
	align-items: center;
	transition: opacity 200ms ease-in-out;
}

.ReactModalPortal .ReactModal__Overlay--after-open {
	opacity: 1;
}
.ReactModalPortal .ReactModal__Overlay--before-close {
	opacity: 0;
}

.modal {
	background: $pink;
	color: $dark-purple-3;
	max-width: 30rem;
	outline: none;
	padding: $l-size;
	text-align: center;
}

.modal__title {
	margin: 0 0 $l-size 0;
}

.modal__selected {
	font-size: 2rem;
	font-weight: 200;
	margin: 0 0 $l-size 0;
	word-break: break-all;
}
